<template>
  <div class="container">
    <h1>All Restaurants</h1>

    <hr>

    <p class="error" v-if="error">{{error}}</p>
    <div class="restaurant-container">
      <div class="restaurant"
          v-for="restaurant in restaurants"
           v-bind:key="restaurant._id"
           v-on:dblclick="deleteRestaurant(restaurant._id)"
      >
        {{restaurant.name}}

      </div>
    </div>

    <div class="create-restaurant">
      <label>Add to db</label>
      <input type="text" id="create-restaurant" v-model="newRestaurant" placeholder="Add a Restaurant">
      <button v-on:click="createRestaurant">Add!</button>
    </div>
  </div>

</template>

<script>
import RestaurantService from "../RestaurantService";

export default {
  name: 'Restaurants',
  data () {
    return {
      restaurants: [],
      error: '',
      newRestaurant: ''
    }
  },
  methods: {
    async createRestaurant () {
      try {
        await RestaurantService.insertRestaurant(this.newRestaurant);
        this.restaurants = await RestaurantService.getRestaurants();
      }
      catch(err) {
        this.error = err.message;
      }
    },
    async deleteRestaurant(id) {
      try {
        await RestaurantService.deleteResturant(id);
        this.restaurants = await RestaurantService.getRestaurants();
      }
      catch(err) {
        this.error = err.message;
      }
    }

  },
  async created () {
    try {
      this.restaurants = await RestaurantService.getRestaurants();
    }
    catch(err) {
      this.error = err.message;
    }
  }
}
</script>

